@use 'sass:color';
@use '../abstracts/variables' as *;

// Form Control
.form-control {
    border-radius: 0;
    font-size: $fontSize;

    &:focus {
        outline: none;
        box-shadow: none;
    }
}

.dropdown-menu {
    border-radius: 4px;
    border: none;
    box-shadow: 0 0 17px rgb(0, 0, 0, 0.1);

    & & {
        left: 100%;
        top: 0;
    }
}

.dropdown-item {
    font-size: $fontSize;
    padding: 8px 16px;
    text-transform: capitalize;
    transition: .3s ease;
    color: rgba(0, 0, 0, .8);
    display: flex;
    align-items: center;

    &.active,
    &.active:hover,
    &:active {
        color: #000;
        background-color: #CDDC39;
    }

    &:hover {
        color: #000;
        background-color: #ededed;
    }
}

.dropdown-item.dropdown-toggle::after {
    transform: rotate(-90deg);
    border-width: 5px;
    opacity: .7;
}

.navbar-nav .dropdown-item {
    display: flex;
    justify-content: space-between;
}

.nav-link.dropdown-toggle::after {
    content: "+";
    border: none;
    vertical-align: baseline;
    margin-left: 8px;
}

.dropdown:hover .nav-link.dropdown-toggle::after {
    content: "-";
}

@media (max-width: 1366px) {
    .container {
        max-width: 1320px;
    }
}

@media (max-width: 992px) {
    .container {
        max-width: 960px;
    }
}

@media (max-width: 768px) {
    .container {
        max-width: 100%;
        padding-left: 15px;
        padding-right: 15px;
    }
}