:root {
  --primary-color: #5352ed;
  --primary-hover: rgb(37.0732984293, 35.8062827225, 232.1937172775);
  --warning-color: #ff7f50;
  --warning-hover: rgb(255, 89.6971428571, 29);
  --success-color: #2ed573;
  --success-hover: rgb(34.8047808765, 173.1952191235, 91.984063745);
  --danger-color: #ff4757;
  --danger-hover: rgb(255, 20, 40.4347826087);
  --dark-color: #000000;
  --dark-hover: hsl(0, 0%, -10%);
  --light-color: #a4b0be;
  --light-hover: rgb(134.25, 150.1730769231, 168.75);
  --info-color: #1e90ff;
  --info-hover: rgb(0, 118.56, 234);
}

body {
  font-size: 14px;
  font-family: "Poppins", "Changa", sans-serif;
  font-weight: 500;
  background-color: whitesmoke;
}

a {
  color: inherit;
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

span {
  display: inline-block;
}

button {
  background-color: transparent;
  border: none;
}

.button {
  display: inline-flex;
  align-items: center;
  border-radius: 4px;
  text-transform: uppercase;
  padding: 12px 20px;
  font-weight: 500;
  transition: 0.3s;
}

.button-sm {
  padding: 10px 16px;
}

.button-md {
  padding: 14px 24px;
}

.button-lg {
  padding: 18px 32px;
}

.button-primary {
  background-color: #5352ed;
  color: white;
}
.button-primary:hover {
  color: white;
  background-color: rgb(19.0497382199, 18.0471204188, 173.4528795812);
}

.button-warning {
  background-color: #ff7f50;
  color: white;
}
.button-warning:hover {
  color: white;
  background-color: rgb(207.5, 55.7285714286, 0);
}

.button-success {
  background-color: #2ed573;
  color: white;
}
.button-success:hover {
  color: white;
  background-color: rgb(22.0039840637, 109.4960159363, 58.1533864542);
}

.button-danger {
  background-color: #ff4757;
  color: white;
}
.button-danger:hover {
  color: white;
  background-color: rgb(198.5, 0, 17.2608695652);
}

.button-dark {
  background-color: #000000;
  color: white;
}
.button-dark:hover {
  color: white;
  background-color: hsl(0, 0%, -25%);
}

.button-light {
  background-color: #a4b0be;
  color: white;
}
.button-light:hover {
  color: white;
  background-color: rgb(94.375, 111.7980769231, 132.125);
}

.button-info {
  background-color: #1e90ff;
  color: white;
}
.button-info:hover {
  color: white;
  background-color: rgb(0, 79.8, 157.5);
}

.tooltip-inner {
  background-color: var(--dark-color);
  color: white;
  text-transform: capitalize;
}

.bs-tooltip-top .tooltip-arrow::before {
  border-top-color: var(--dark-color);
}

.bs-tooltip-bottom .tooltip-arrow::before {
  border-bottom-color: var(--dark-color);
}

.bs-tooltip-start .tooltip-arrow::before {
  border-left-color: var(--dark-color);
}

.bs-tooltip-end .tooltip-arrow::before {
  border-right-color: var(--dark-color);
}

.bs-tooltip-auto[data-popper-placement^=bottom] .tooltip-arrow::before,
.bs-tooltip-bottom .tooltip-arrow::before {
  border-bottom-color: var(--dark-color);
}

.form-control {
  border-radius: 0;
  font-size: 14px;
}
.form-control:focus {
  outline: none;
  box-shadow: none;
}

.dropdown-menu {
  border-radius: 4px;
  border: none;
  box-shadow: 0 0 17px rgba(0, 0, 0, 0.1);
}
.dropdown-menu .dropdown-menu {
  left: 100%;
  top: 0;
}

.dropdown-item {
  font-size: 14px;
  padding: 8px 16px;
  text-transform: capitalize;
  transition: 0.3s ease;
  color: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
}
.dropdown-item.active, .dropdown-item.active:hover, .dropdown-item:active {
  color: #000;
  background-color: #CDDC39;
}
.dropdown-item:hover {
  color: #000;
  background-color: #ededed;
}

.dropdown-item.dropdown-toggle::after {
  transform: rotate(-90deg);
  border-width: 5px;
  opacity: 0.7;
}

.navbar-nav .dropdown-item {
  display: flex;
  justify-content: space-between;
}

.nav-link.dropdown-toggle::after {
  content: "+";
  border: none;
  vertical-align: baseline;
  margin-left: 8px;
}

.dropdown:hover .nav-link.dropdown-toggle::after {
  content: "-";
}

@media (max-width: 1366px) {
  .container {
    max-width: 1320px;
  }
}
@media (max-width: 992px) {
  .container {
    max-width: 960px;
  }
}
@media (max-width: 768px) {
  .container {
    max-width: 100%;
    padding-left: 15px;
    padding-right: 15px;
  }
}
.app-header {
  position: relative;
}

.app-header::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  background: linear-gradient(45deg, #f2f1e2, #dcecdd);
  z-index: -1;
}

.header {
  padding: 18px 0;
}

.header-logo a {
  font-weight: 700;
  font-size: 24px;
  letter-spacing: 0.5px;
}
.header-logo svg {
  width: 40px;
  height: 32px;
  color: var(--primary-color);
}

.header-search-area {
  display: flex;
  align-items: center;
  border: 1px solid #e5e7eb;
  padding: 6px 8px;
  background-color: #fff;
  border-radius: 4px;
}

.search-category-list {
  width: 180px;
}
.search-category-list svg {
  margin-right: 12px;
  width: 22px;
}

.dropdown-item.primary svg {
  color: #5352ed;
}

.dropdown-item.warning svg {
  color: #ff7f50;
}

.dropdown-item.success svg {
  color: #2ed573;
}

.dropdown-item.danger svg {
  color: #ff4757;
}

.dropdown-item.dark svg {
  color: #000000;
}

.dropdown-item.light svg {
  color: #a4b0be;
}

.dropdown-item.info svg {
  color: #1e90ff;
}

.header-social-links {
  align-self: center;
}
.header-social-links .nav {
  justify-content: end;
}
.header-social-links a {
  padding: 0 8px;
}
.header-social-links svg {
  width: 20px;
  color: var(--dark-color, #000);
}
@media (max-width: 1200px) {
  .header-social-links {
    display: none;
  }
}

.auth img {
  width: 38px;
  height: 38px;
  border-radius: 50%;
  object-fit: cover;
}

.navbar .navbar-collapse {
  background-color: #fff;
  box-shadow: 0 0 22px rgba(0, 0, 0, 0.1);
  padding: 8px 12px;
}
.navbar .dropdown-menu {
  display: block;
  transition: 0.3s;
  transform: translateY(10px);
  visibility: hidden;
  opacity: 0;
  padding: 1rem;
}
.navbar .dropdown:hover > .dropdown-menu {
  cursor: pointer;
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}
.navbar .nav-link {
  font-weight: 600;
  text-transform: uppercase;
  pointer-events: none;
}
.navbar span.count {
  background-color: #00BCD4;
  position: absolute;
  width: 18px;
  height: 18px;
  border-radius: 50px;
  font-size: 13px;
  color: white;
  right: 10px;
  top: 11px;
}
.navbar .shopping-cart {
  position: relative;
}
.navbar .shopping-cart span {
  background-color: var(--primary-color);
  color: white;
  position: absolute;
  width: 16px;
  border-radius: 50px;
  height: 16px;
  font-size: 11px;
  left: 32px;
  top: 10px;
}
@media (max-width: 1024px) {
  .navbar .phone, .navbar .mail {
    display: none;
  }
}

.hero-section {
  padding: 84px 0;
}

.hero-text h2 {
  font-size: 64px;
  font-weight: 700;
  text-transform: capitalize;
}

.hero-text p {
  font-size: 16px;
  margin: 24px 0;
  text-transform: capitalize;
  line-height: 32px;
}

.hero-text h2 span {
  background-color: var(--warning-color);
  color: white;
  padding: 0 8px;
}

.hero-image {
  position: relative;
  text-align: right;
  z-index: 1;
}

.hero-image img {
  height: 550px;
  right: 8%;
  bottom: 0;
  top: -50px;
}

.hero-image::before {
  position: absolute;
  content: "";
  width: 500px;
  height: 500px;
  background: linear-gradient(to bottom, #fff, transparent);
  border-radius: 50%;
  right: 0;
  z-index: -1;
  top: 0;
}

.floating-img {
  position: absolute;
  transition: transform 0.1s linear;
  pointer-events: none;
}

img.floating-img.book {
  left: 54%;
  bottom: 20px;
}

img.floating-img.red-plus {
  top: 40%;
  left: 45%;
}

img.floating-img.crown {
  top: 50%;
  right: 140px;
  z-index: 1;
}

img.floating-img.dots {
  top: 180px;
  right: 30px;
}